/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
a.btn, .btn, button,
input[type=button],
input[type=reset],
input[type=submit] {
  display: inline-flex;
  position: relative;
  text-decoration: none;
  line-height: 1;
  border-radius: 6px;
  font-weight: bold;
  transition: all 0.3s ease;
  border: 0;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
a.btn:active, .btn:active, button:active,
input[type=button]:active,
input[type=reset]:active,
input[type=submit]:active {
  transition: none;
}
a.btn span, a.btn strong, .btn span, .btn strong, button span, button strong,
input[type=button] span,
input[type=button] strong,
input[type=reset] span,
input[type=reset] strong,
input[type=submit] span,
input[type=submit] strong {
  margin: 0 calc(0.5 * 1rem) 0 calc(0.5 * 1rem);
}
a.btn .icon, .btn .icon, button .icon,
input[type=button] .icon,
input[type=reset] .icon,
input[type=submit] .icon {
  /* icon inherits color of button label */
  color: inherit;
  flex-shrink: 0;
}

/* Main buttons */
a.btn--primary, .btn--primary {
  background-color: #663399;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a.btn--primary:hover, .btn--primary:hover {
  background-color: #320055;
  color: #ffffff;
}

a.btn--secondary, .btn--secondary {
  background-color: #c2185b;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a.btn--secondary:hover, .btn--secondary:hover {
  background-color: #663399;
  color: #ffffff;
}

a.btn--white, .btn--white {
  background-color: #ffffff;
  color: #663399;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a.btn--white:hover, .btn--white:hover {
  color: #320055;
}

/* feedback */
.btn[disabled] {
  cursor: not-allowed;
  background-color: #c4cdd5;
  color: #ffffff;
  box-shadow: none;
  opacity: 0.6;
}
.btn[disabled]:visited {
  color: var(--btn-disabled-label);
}

/* button size */
.a.btn--sm, .btn--sm {
  padding: calc(0.5 * 1rem) calc(1.5 * 1rem);
  height: 32px;
}

a.btn--md, .btn--md {
  padding: 0 calc(2 * 1rem);
  height: 40px;
}

a.btn--lg, .btn--lg {
  padding: calc(1.5 * 1rem) calc(2.5 * 1rem);
  height: 48px;
}

.btn--full {
  display: flex;
  width: 100%;
  justify-content: center;
}

/* buttons group */
.btn-group {
  display: flex;
  flex-wrap: wrap;
}
.btn-group > * {
  margin-right: calc(1.5 * 1rem);
}
.btn-group > *:last-of-type {
  margin-right: 0;
}

